.notificationContainer{
    position: fixed;
    bottom: 1rem;
    left: 1rem;
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 1;
}
.notification{
    width: 380px;
    padding: .5rem 1rem;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    border-radius: 2px;
}
.notificationIcon{
    width: 22px;
    height: 22px;
    margin-right: 15px;
}
.closeNotification{
    width: 10px;
    height: 10px;
    cursor: pointer;
}
.notificationBar{
    position: absolute;
    width: 0;
    height: 3px;
    bottom: 0;
    left: 0;
}
.notificationMsgContainer{
    display: flex;
    flex-direction: column;
}
.notificationMsgContainer li p{
    font-size: 0.7rem;
}


@media (max-width: 420px){
    .notificationContainer{
        width: 100%;
        left: 0;
        bottom: 0;
    }
    .notification{
        width: 100%;
        margin: 0;
        padding: .5rem 1rem;
        border-radius: 0px;
    }
}