.footer{
    width: 100%;
    min-height: 60vh;
    background-image: url("../../asset/image/space.gif");
    background-color: var(--dark-purple);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;
}
.footer-container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    color: var(--white);
    padding: 0 10rem;
}
.footer-main{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.footer-main hr{
    width: 100%;
    color: var(--white);
    margin-top: 1rem;
}
.footer-form{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 50%;
}
.footer-form input,
.footer-form textarea{
    width: 100%;
    height: 40px;
    background-color: transparent;
    border: none;
    resize: none;
    color: var(--white);
    font-size: 1rem;
}
.footer-form textarea{
    height: 100px;
}
.footer-form input::placeholder,
.footer-form textarea::placeholder{
    color: var(--white);
}
.footer-form input:focus,
.footer-form textarea:focus{
    outline: 0;
}
.footer-form button{
    background-color: transparent;
    color: var(--white);
    width: 100px;
    height: 50px;
    margin-top: 0.5rem;
    font-size: 1rem;
    border: none;
    background-color: rgba(0, 0, 0, 0.448);
}
.footer-form form{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* border: 2px solid var(--white); */
    padding: 2rem;
    gap: .5rem;
}
.footer-form-names {
    display: flex;
    gap: .5rem;
}
.footer-main-head{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer-socials{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.footer-socials svg{
    fill: var(--white);
    margin-left: 1rem;
}
.footer-main-contact{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
}
.footer-main-contact li{
    display: flex;
    gap: 1rem;
}
.footer-main p{
    color: var(--white);
}
.footer-main-message{
    padding-top: 1rem;
}
.footer-main-contact svg{
    fill: var(--white);
    height: 25px;
    width: 25px;
}
.footer-clock svg{
    fill: var(--white);
    height: 25px;
    width: 25px;
}
.footer-clock{
    padding-top: 1rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}
.footer-input-container{
    background-color: rgba(0, 0, 0, 0.448);
    display: flex;
    align-items: center;
    padding: .5rem;
    padding-left: 1rem;
    gap: .5rem;
    width: 100%;
}
.footer-input-container svg{
    fill: var(--white);
    width: 20px;
    height: 20px;
}
.footer-textarea{
    align-items: flex-start;
}


@media (max-width: 1200px){
    .footer-container{
        padding: 0 5rem;
    }
}

@media (max-width: 1024px){
    .footer-container{
        padding: 0 2rem;
    }
    .footer-main,
    .footer-form{
        padding-top: 2rem;
        width: 100%;
    }
    .footer-form{
        padding-bottom: 2rem;
    }
    .footer-form form{
        width: 90%;
        padding: 0;
    }
}

@media (max-width: 768px){
    .footer{
        margin-top: 0;
    }
    .footer-container{
        flex-direction: column;
    }
    .footer-form form{
        width: 100%;
    }
}

@media (max-width: 480px){
}
