@font-face {
  font-family: BoldTTF;
  src: url(./asset/font/Bold.ttf);
}
@font-face {
  font-family: BoldWOFF;
  src: url(./asset/font/Bold.woff);
}
@font-face {
  font-family: BoldWOFF2;
  src: url(./asset/font/Bold.woff2);
}
@font-face {
  font-family: RegularTTF;
  src: url(./asset/font/Regular.ttf);
}
@font-face {
  font-family: RegularWOFF;
  src: url(./asset/font/Regular.woff);
}
@font-face {
  font-family: RegularWOFF2;
  src: url(./asset/font/Regular.woff2);
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --white: #fff;
  --grey: #494949;
  --black: #000000;
  --purple: #510BA1ff;
  --russian-violet: #11013Aff;
  --dark-purple: #0A0126ff;
  --amethyst: #AB62EDff;
  --heliotrope: #C579F6ff;
  --screen-width: 100vw;
}
body{
  background-color: var(--white);
}
h1{
  font-family: BoldWOFF2, BoldWOFF, BoldTTF;
  font-weight: lighter;
  line-height: 1em;
}
p{
  font-family: RegularTTF, RegularWOFF, RegularWOFF2;
  color: var(--grey);
  font-size: 1rem;
  line-height: 1.5em;
}
strong{
  color: var(--black);
}
a{
  text-decoration: none;
}
li{
  list-style: none;
  text-decoration: none;
}
button{
  cursor: pointer;
}
